.v-distribute, %v-distribute {
	display: flex;
	flex-direction: column;
	> * {
		flex-shrink:0;
	}
}

	.v-grow, %v-grow {
		flex-grow: 1;
	}
	.v-grow-plus, %v-grow-plus {
		flex-grow: 2;
	}
	.v-shrink {
		flex-shrink: 1;
	}
	.v-align-content {
		display: flex;
		flex-direction: column;
	  justify-content: center;
	}
