.theme--dark, %theme--dark {
	background:url(../img/background-small.png) no-repeat center center;
	background-size: cover;
	background-color:$color-primary;
	color:white;
	@include media-query('tab') {
		background-image:url(../img/background-medium.png);
	}
	@media only screen and (min-width: media-rem(1200)) {
		background-image:url(../img/background-large.png);
	}
}

	
