.faq-section {

}

.faq-question {
	h2 {
		margin-bottom:$space-half;
		font-size:inherit;
		font-weight:inherit;
	}
}

.faq-top-link {
	position:fixed;
	top:0;
	left:0;
	width:100%;	
	padding:.1em;
	color:$color-primary;
	font-weight:normal;
	font-size:$size-medium;
	text-align:right;
	a {
		position:absolute;
		top:0;
		right:0;
		padding:2rem .5rem .5rem .5rem;
		background-color:rgba(#fff,0.8);
		&:after {
			position:relative;
			top:-.1em;
	    display: inline-block;
	    content:" ";
	    width: 2.2rem;
	    height: 2.2rem;
	    vertical-align: middle;
	    background: url('../img/svg/arrow-pointer-circle-up.svg') no-repeat center center;
	    background-size: 22px 22px;
	  }
	}
}
