.cta {
	color:$color-secondary;
	font-weight:bold;
	background:transparent;
	border:0;
	padding:0;
	line-height:inherit;
	vertical-align:bottom;
	&:hover {
		color:$color-secondary-hover;
	}
	&:focus {
		color:$color-secondary-focus;
		outline:0;
	}
	img {
		margin-left:0.2em;
		vertical-align: middle;
	}
}

	.cta--primary {
		color:$color-primary;
		&:hover {
			color:$color-primary-hover;
		}
		&:focus {
			color:$color-primary-focus;
			outline:0;
		}
	}

.block-cta-list {
	border-top: $grey-light solid 1px;
	.block-cta {
		border-top:none;
	}
}
.block-cta {
	display:block;
	border: $grey-light solid 1px;
	border-width: 1px 0;
	padding: 1.5rem .5rem;
	> a {
		display: block;
	}
	&--remove a:after {
		background-image: url('../img/svg/cross-blue.svg');
		background-size:18px 18px;
	}
}
.block-cta--big {
	position:relative;
	display: flex;
	flex-direction: column;
  justify-content: center;
	height:9.5rem;
	> a {
		font-weight:normal;
		padding-right:6rem;
		&:after {
			content: " ";
			background: url('../img/svg/arrow-circle-right.svg') no-repeat center center;
			background-size:36px 36px;
			position:absolute;
			top:50%;
			margin-top: -1.6rem;
			right:1.5rem;
			width:3.7rem;
			height:3.7rem;
		}
	}

	p {
		margin:0;
	}
	& + & {
		border-top:none;
	}
}

	.block-cta__heading {
		color:inherit;
		font-weight: bold;
		font-size: $size-med-double-plus;
		margin-bottom:.2em;
	}
