.principals-panel {
	background:$grey-xlight;
	text-align:center;
	.cta {
		padding-left:1.5rem;
	}
}
	.principals-panel__content {
		padding-top:3.5rem;
		padding-bottom:1.5rem;
	}
		.principals-panel__list {
			margin-bottom: 1rem;
		}
		.principals-panel__icon {
			line-height:4.5rem;
			margin-bottom: .5em;
			img {
				vertical-align: middle;
			}
		}
		.principals-panel__close {
			position:absolute;
			top:1rem;
			right:1.5rem;
		}

.principals-summary {
	margin-left:-1rem;
	li {
		display: flex;
		margin-bottom:$space;
	}
	p {
		margin:0;
	}
}
.no-flexbox {
	.principals-summary li {
		@extend .cf;
	}
	.principals-summary__icon {
		padding-top:.5rem;
		float:left;
	}
	.principals-summary__info {
		overflow: hidden;
	}
}

	.principals-summary__heading {
		color:inherit;
		margin:0;
	}

	.principals-summary__info {
		flex-grow:1;
	}
	.principals-summary__icon {
		display: flex;
		flex-direction: column;
	  justify-content: center;
		flex-shrink:0;
	  margin-right:1rem;
		width: 5rem;
		text-align: center;
		img {
			display: initial;
		}
	}
