.account-select {

}

	.account-select__mode-btn {
		.text-done {
			display: none;
		}
		

	}

	.account-select__item {
		.account-select--remove & {
			@extend .block-cta--remove;
		}
	}

	.account-select--remove {
		.account-select__nav-item {
			display: none;
		} 
		.account-select__item {
			@extend .block-cta--remove;
		}
		.account-select__mode-btn {
			.text-remove {
				display: none;
			}
			.text-done {
				display: block;
			}
		}
	}

.accounts-page--empty {
	.accounts-page__empty-content {
		display:block;
	}
	.accounts-page__default-content {
		display:none;
	}
}
.accounts-page__empty-content {
	display:none;
}
